.history {
  padding-bottom: 15px;
  border-bottom: 1px solid #ebebeb;
  @include media-breakpoint-up(md) {
    padding-bottom: 9px;
  }
  @include media-breakpoint-up(xl) {
    padding-top: 78px;
    padding-bottom: 70px;
  }
  &__inner {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 102px;
      left: 8px;
      border-bottom: 1px solid $secondary-red;
      display: none;
      @include media-breakpoint-up(md) {
        display: block;
        width: calc(768px + (100vw - 768px / 2));
      }
      @include media-breakpoint-up(lg) {
        width: calc(960px + (100vw - 960px / 2));
      }
      @include media-breakpoint-up(xl) {
        left: 13px;
        top: 110px;
        width: calc(1344px + (100vw - 1344px / 2));

      }
    }
  }
  &__title {
    @include media-breakpoint-up(xl) {
      margin-bottom: 35px;
    }
  }
  &__item {
    margin-bottom: 12px;
    &--first {
      @include media-breakpoint-up(md) {
        .history__desc {
          max-width: 170px;
        }
      }
      @include media-breakpoint-up(xl) {
        .history__desc {
          max-width: 265px;
        }
      }
    }
    &--second {
      @include media-breakpoint-up(md) {
        .history__desc {
          max-width: 175px;
        }
      }
      @include media-breakpoint-up(xl) {
        .history__desc {
          max-width: 267px;
        }
      }
    }
    &--third {
      @include media-breakpoint-up(md) {
        .history__desc {
          max-width: 165px;
        }
      }
      @include media-breakpoint-up(xl) {
        .history__desc {
          max-width: 282px;
        }
      }
    }
  }
  &__year {
    font-size: 54px;
    line-height: 74px;
    text-align: center;
    color: $primary-red;
    letter-spacing: 0;
    @include media-breakpoint-up(md) {
      position: relative;
      padding-bottom: 43px;
      display: block;
      text-align: left;
    }
    @include media-breakpoint-up(xl) {
      padding-bottom: 70px;
      font-size: 70px;
      line-height: 80px;
    }
    &::before {
      content: '';
      display: none;
      position: absolute;
      bottom: 10px;
      left: 8px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $primary-red;
      @include media-breakpoint-up(md) {
        display: block;
      }
      @include media-breakpoint-up(xl) {
        bottom: 35px;
        left: 13px;
      }
    }

    span {
      padding-left: 8px;
    }
  }
  &__desc {
    margin-bottom: 0;
    line-height: 20px;
    letter-spacing: 0.3px;
    @include media-breakpoint-up(md) {
      font-size: 16px;
      line-height: 22px;
    }
    @include media-breakpoint-up(xl) {
      line-height: 24px;
    }
  }
}
